import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/checkout/components/EmptyCart/EmptyCart.css';
import { Button, Icon, IllustratedMessage } from '@adobe/elsie/components';
import { Cart } from '@adobe/elsie/icons';
import { useText } from '@adobe/elsie/i18n';

export interface EmptyCartProps extends HTMLAttributes<HTMLDivElement> {
  title?: string;
  routeHome?: () => string;
}

export const EmptyCart: FunctionComponent<EmptyCartProps> = ({
  className,
  title,
  routeHome,
  ...props
}) => {
  const translations = useText({
    Title: 'Checkout.EmptyCart.title',
    Button: 'Checkout.EmptyCart.button',
  });

  return (
    <IllustratedMessage
      {...props}
      data-testid="checkout-empty-cart"
      className={classes(['checkout-empty-cart', className])}
      heading={title ?? translations.Title}
      icon={
        <Icon
          className={classes(['checkout-empty-cart__icon'])}
          source={Cart}
        />
      }
      action={
        routeHome?.() ? (
          <Button
            className="checkout-empty-cart__button"
            size="medium"
            variant="primary"
            key="routeHome"
            type="submit"
            href={routeHome?.()}
          >
            {translations.Button}
          </Button>
        ) : undefined
      }
    />
  );
};
