import { useEffect } from 'preact/hooks';
import { events } from '@adobe/event-bus';
import { isAuthenticatedSignal } from '@/checkout/signals/AuthenticationSignal';
import { CheckoutEvent, useCheckoutState } from '@/checkout/context/checkout';

export function useAuthentication() {
  const { dispatch } = useCheckoutState();

  useEffect(() => {
    const onAuthenticated = events.on(
      'authenticated',
      (isAuthenticated) => {
        isAuthenticatedSignal.value = isAuthenticated;

        if (isAuthenticated)
          dispatch({ type: CheckoutEvent.USER_AUTHENTICATED });
        else dispatch({ type: CheckoutEvent.USER_UNAUTHENTICATED });
      },
      { eager: true }
    );

    return () => {
      onAuthenticated?.off();
    };
  }, [dispatch]);
}
