import { FunctionComponent, JSX } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/checkout/components/OutOfStock/OutOfStock.css';
import { Text, useText } from '@adobe/elsie/i18n';
import { Button, Icon, IllustratedMessage } from '@adobe/elsie/components';
import { OrderError } from '@adobe/elsie/icons';

export interface OutOfStockProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'icon'> {
  icon?: FunctionComponent<JSX.SVGAttributes<SVGElement>>;
  routeCart?: () => string;
}

export const OutOfStock: FunctionComponent<OutOfStockProps> = ({
  className,
  icon,
  routeCart,
  ...props
}) => {
  const translations = useText({
    Title: 'Checkout.OutOfStock.title',
    Message: 'Checkout.OutOfStock.message',
  });

  return (
    <div
      {...props}
      className={classes(['checkout-out-of-stock', className])}
      data-testid="checkout-out-of-stock"
    >
      <IllustratedMessage
        heading={translations.Title}
        message={<p>{translations.Message}</p>}
        icon={
          <Icon
            source={icon ?? OrderError}
            className={classes(['checkout-out-of-stock__icon'])}
          />
        }
      />

      {routeCart?.() && (
        <Button
          className="checkout-out-of-stock__button"
          size="medium"
          variant="primary"
          key="routeCart"
          type="submit"
          href={routeCart?.()}
        >
          <Text id="Checkout.OutOfStock.button" />
        </Button>
      )}
    </div>
  );
};
