import { ProductStockStatus } from '@/checkout/__generated__/types';
import {
  getCart,
  getCustomer,
  getStockStatus,
  placeOrder,
  UnexpectedError,
} from '@/checkout/api';
import {
  Checkout as CheckoutComponent,
  EmptyCart,
  OutOfStock,
  ServerError,
} from '@/checkout/components';
import {
  BillingForm,
  BillToShippingAddress,
  CartSummary,
  ErrorBanner,
  LoginForm as Login,
  OrderSummary,
  PaymentMethods,
  PlaceOrder,
  ShippingForm,
  ShippingMethods,
} from '@/checkout/containers';
import { PaymentMethodSlotContext } from '@/checkout/containers/PaymentMethods';
import {
  CheckoutEvent,
  CheckoutState,
  useCheckoutState,
} from '@/checkout/context/checkout';
import { useAuthentication } from '@/checkout/hooks/useAuthentication';
import { cartSignal, isBillToShippingSignal } from '@/checkout/signals';
import { isAuthenticatedSignal } from '@/checkout/signals/AuthenticationSignal';
import { useText } from '@adobe/elsie/i18n';
import { classes, Slot, SlotProps } from '@adobe/elsie/lib';
import { events } from '@adobe/event-bus';
import {
  HTMLAttributes,
  useCallback,
  useEffect,
  useState,
} from 'preact/compat';
import { Divider } from '@adobe/elsie/components';

export type PreselectedAddressFields = {
  countryCode?: string;
  region?: string;
  postCode?: string;
};

export type PreselectedShippingMethod = {
  carrierCode: string;
  methodCode: string;
};

export type PreselectedCartData = {
  address?: PreselectedAddressFields;
  shippingMethod?: PreselectedShippingMethod;
};

export interface CheckoutProps extends HTMLAttributes<HTMLDivElement> {
  routeHome?: () => string;
  routeCart?: () => string;
  slots?: {
    BillingForm?: SlotProps;
    BillToShipping?: SlotProps;
    CartSummary?: SlotProps;
    Login?: SlotProps;
    OrderSummary?: SlotProps;
    PaymentMethods?: SlotProps<PaymentMethodSlotContext>;
    PlaceOrder?: SlotProps;
    ShippingForm?: SlotProps;
    ShippingMethods?: SlotProps;
  };
  preselectedCartData?: PreselectedCartData;
  onSignInClick?: () => void;
  onSignOutClick?: () => void;
}

export const Checkout = (props: CheckoutProps) => {
  const {
    className,
    slots,
    routeHome,
    routeCart,
    preselectedCartData,
    onSignInClick,
    onSignOutClick,
    ...restProps
  } = props;
  const { state, dispatch } = useCheckoutState();

  const [serverError, setServerError] = useState('');
  const hasErrors = serverError !== '';

  const [handlePlaceOrder, setHandlePlaceOrder] =
    useState<() => Promise<void> | undefined>();

  const [hasOutOfStock, setHasOutOfStock] = useState(false);

  const cart = cartSignal.value.data;
  const isVirtual = Boolean(cart?.virtual);

  const translations = useText({
    CheckoutUnexpectedError: 'Checkout.ServerError.unexpected',
  });

  const defaultPlaceOrderHandler = useCallback(async () => {
    if (!cart) return;
    await placeOrder(cart.id);
    cartSignal.value = { pending: false };
    dispatch({ type: CheckoutEvent.ORDER_PLACED });
  }, [cart, dispatch]);

  const onPlaceOrder = (fn: () => Promise<void>) => {
    setHandlePlaceOrder(() => fn);
  };

  const dismissError = async () => {
    setServerError('');
    if (!cart) return;

    if (isAuthenticatedSignal.value) {
      await loadCustomerData();
    } else {
      await loadGuestData(cart.id);
    }
  };

  const handleServerError = (error: any) => {
    setServerError(
      error instanceof TypeError || error instanceof UnexpectedError
        ? translations.CheckoutUnexpectedError
        : error.message
    );
  };

  const loadGuestData = useCallback(
    async (cartId: string) => {
      const cart = await getCart(cartId);
      dispatch({ type: CheckoutEvent.CART_UPDATED, payload: cart });
    },
    [dispatch]
  );

  const loadCustomerData = useCallback(async () => {
    const cart = await getCart();
    await getCustomer();
    dispatch({ type: CheckoutEvent.CART_UPDATED, payload: cart });
  }, [dispatch]);

  const checkStockStatus = async (cartId?: string) => {
    const result = await getStockStatus(cartId);
    setHasOutOfStock(result === ProductStockStatus.OutOfStock);
  };

  useAuthentication();

  useEffect(() => {
    const onCartData = events.on(
      'cart/data',
      (cart) => {
        if (isAuthenticatedSignal.value) {
          loadCustomerData();
          checkStockStatus();
          return;
        }

        if (cart && cart.id) {
          cartSignal.value = { ...cartSignal.value, pending: true };
          loadGuestData(cart.id);
          checkStockStatus(cart.id);
          return;
        }

        cartSignal.value = { data: null, pending: false };
        dispatch({ type: CheckoutEvent.CART_UPDATED, payload: null });
      },
      { eager: true }
    );

    return () => {
      onCartData?.off();
    };
  }, [dispatch, loadCustomerData, loadGuestData]);

  if (state === CheckoutState.COMPLETE) {
    return null;
  }

  if (state === CheckoutState.EMPTY) {
    return <EmptyCart routeHome={routeHome} />;
  }

  if (hasErrors) {
    return <ServerError errorMessage={serverError} onClick={dismissError} />;
  }

  const renderMain = () => {
    if (hasOutOfStock) {
      return (
        <CheckoutComponent.Main
          outOfStock={<OutOfStock routeCart={routeCart} />}
        />
      );
    }

    const billingAddressVisibilityToggle = isBillToShippingSignal.value.checked
      ? 'checkout-billing-form__hidden'
      : 'checkout-billing-form__visible';

    return (
      <CheckoutComponent.Main
        sections={{
          billingAddress: (
            <>
              <Slot
                name="BillingForm"
                className={billingAddressVisibilityToggle}
                slot={slots?.BillingForm}
              >
                <BillingForm />
              </Slot>
              <Divider
                className={classes([
                  `${billingAddressVisibilityToggle}`,
                  'checkout-billing-form__divider',
                ])}
                variant="primary"
              />
            </>
          ),
          billToShippingAddress: isVirtual ? undefined : (
            <Slot name="BillToShipping" slot={slots?.BillToShipping}>
              <BillToShippingAddress />
            </Slot>
          ),
          login: (
            <Slot name="Login" slot={slots?.Login}>
              <Login
                onSignInClick={onSignInClick}
                onSignOutClick={() => {
                  dispatch({ type: CheckoutEvent.USER_SIGN_OUT });
                  onSignOutClick?.();
                }}
              />
            </Slot>
          ),
          paymentMethods: (
            <PaymentMethods
              isShippingInfoRequired={!isVirtual}
              onPlaceOrder={onPlaceOrder}
              handleServerError={handleServerError}
              paymentMethodsSlot={slots?.PaymentMethods}
            />
          ),
          placeOrder: (
            <PlaceOrder
              onClick={handlePlaceOrder ?? defaultPlaceOrderHandler}
              handleServerError={handleServerError}
            />
          ),
          shippingAddress: isVirtual ? undefined : (
            <Slot name="ShippingForm" slot={slots?.ShippingForm}>
              <ShippingForm preselectedFields={preselectedCartData?.address} />
            </Slot>
          ),
          shippingMethods: isVirtual ? undefined : (
            <Slot name="ShippingMethods" slot={slots?.ShippingMethods}>
              <ShippingMethods
                preSelectedMethod={preselectedCartData?.shippingMethod}
              />
            </Slot>
          ),
        }}
      />
    );
  };

  const renderAside = () => {
    return (
      <CheckoutComponent.Aside
        sections={{
          cartSummary: (
            <Slot name="CartSummary" slot={slots?.CartSummary}>
              <CartSummary routeCart={routeCart} />
            </Slot>
          ),
          orderSummary: (
            <Slot name="OrderSummary" slot={slots?.OrderSummary}>
              <OrderSummary />
            </Slot>
          ),
        }}
      />
    );
  };

  return (
    <CheckoutComponent
      className={className}
      banner={<ErrorBanner />}
      isLoading={state === CheckoutState.LOADING}
      {...restProps}
    >
      {renderAside()}
      {renderMain()}
    </CheckoutComponent>
  );
};
